/* src/components/About.css */
.about-section {
    padding: 4rem 2rem;
    margin: auto;
    border-top: 2px solid #fff;
    text-align: justify;
    background-color: black;
    color: white;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    transition: box-shadow 0.3s ease;
  }
  
  .about-section:hover {
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  }
  
  .about-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    text-align: center;
    color: #fff;
  }
  
  .about-text {
    font-size: 1.125rem;
    line-height: 1.8;
    color: #333333;
  }
  
  .about-text p {
    margin-bottom: 1.5rem;
  }
  